import { fromJS } from 'immutable';
import { createAction, handleActions } from 'redux-actions';

const newDot = createAction('CREATING/NEW_DOT')
const setDotData = createAction('CREATING/SET_DOT_DATA')
const removeDot = createAction('CREATING/REMOVE_DOT')
const setAnswer = createAction('CREATING/SET_ANSWER')
const setYesNo = createAction('CREATING/SET_YES_NO')
const clearData = createAction('CREATING/CLEAR_DATA')

const initialState = fromJS({
  dots: [],
  yesNo: {},
  answers: {},
});

const reducer = handleActions({
  [newDot]: (state, { payload }) => state.update('dots', list => list.push(fromJS({ position: payload }))),
  [setDotData]: (state, { payload: { index, pain, duration } }) =>
    state.updateIn(['dots', index], dot => dot.set('pain', pain).set('duration', duration)),
  [removeDot]: (state, { payload }) => state.removeIn(['dots', payload]),
  [setYesNo]: (state, { payload: { name, value } }) => state.setIn(['yesNo', name], value),
  [setAnswer]: (state, { payload: { name, value } }) => state.setIn(['answers', name], value),
  [clearData]: () => initialState,
}, initialState);

export {
  newDot,
  setDotData,
  removeDot,
  setAnswer,
  setYesNo,
  clearData,
};

export default reducer;
